@font-face {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 400;
  src: local(Open Sans Regular), local(OpenSans-Regular), url("open-sans-v17-latin_cyrillic-regular.fd08d484.woff") format("woff");
}

@font-face {
  font-family: Open Sans;
  font-style: italic;
  font-weight: 400;
  src: local(Open Sans Italic), local(OpenSans-Italic), url("open-sans-v17-latin_cyrillic-italic.fed68991.woff") format("woff");
}

@font-face {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: 400;
  src: local(Roboto Condensed), local(RobotoCondensed-Regular), url("roboto-condensed-v18-latin_cyrillic-regular.635c9d3f.woff") format("woff");
}

body {
  color: #2c3e50;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Open Sans, Helvetica Neue, sans-serif;
  font-size: 14pt;
  line-height: 130%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

textarea {
  padding: .6rem;
  font-family: Open Sans, Helvetica Neue, sans-serif;
  font-size: 14pt;
}

h1, h2, h3 {
  font-family: Roboto Condensed, Helvetica Neue, sans-serif;
}

